<template>
    <div>
        <div v-for="(cartItem, index) in cart" :key="index" lg="4">
            <div class="basket__content">
                <v-img v-if="cartItem.product && cartItem.product.image" :src="cartItem.product.image" alt="Product Image" 
                max-width="120px" max-height="120px">
                </v-img>
                <div class="basket__contentContext pl-5">
                    <p class="basket__content--titre mb-0">{{ cartItem.product && cartItem.product.name }}</p>
                    <p class="basket__content--text ellipsis mb-0">{{ cartItem.product && cartItem.product.description }}</p>
                    <p class="basket__content--text mb-0">Quantité: {{ cartItem.quantity || 0 }} - Prix: {{ cartItem.product.price }} €</p>
                    <div class="d-flex justify-end">
                        <v-btn class="mx-1" elevation="0" fab dark x-small color="#fb9f89ff" @click="removeProduct(index)">
                            <v-icon dark>
                                mdi-delete
                            </v-icon>
                        </v-btn>

                        <v-btn elevation="0" v-if="cartItem.quantity > 1" class="mx-1" fab dark x-small color="#fb9f89ff" @click="decrementQuantity(index)">
                            <v-icon dark>
                                mdi-minus
                            </v-icon>
                        </v-btn>

                        <v-btn elevation="0" class="mx-1" fab dark x-small color="#fb9f89ff" @click="incrementQuantity(index)">
                            <v-icon dark>
                                mdi-plus
                            </v-icon>
                        </v-btn>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'basketView',
    computed: {
        cart() {
            // Use the cart from the store
            return this.$store.state.cart;
        },
    },
    methods: {
        goToAboutPage() {
            this.$router.push({ name: 'About' });
        },
        removeProduct(index) {
            // Remove the product from the basket by its index
            this.$store.commit('removeFromCart', index);
        },
        decrementQuantity(index) {
            // Dispatch the action to decrement quantity
            this.$store.dispatch('decrementProductQuantity', index);
        },
        incrementQuantity(index) {
            // Get the cartItem at the specified index
            const cartItem = this.cart[index];

            // Check if the cartItem exists and has a product property
            if (cartItem && cartItem.product && typeof cartItem.quantity === 'number') {
                // Increment the quantity
                cartItem.quantity++;

                // Commit a mutation to update the cart item in the store
                this.$store.commit('updateCartItem', { index, cartItem });
            }
        },
    },
};
</script>

