// src/router.js

import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import Contact from './views/Contact.vue';
import Basket from './views/Basket.vue';
import AdminLogin from './views/AdminLogin.vue';
import backOffice from './components/backOffice.vue';
import firebase from 'firebase/app';
import 'firebase/auth'; // Importez Firebase Authentication
// import store from './store'; // Import your Vuex store


Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
    component: About,
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
  },
  {
    path: '/basket',
    name: 'Basket',
    component: Basket,
  },
  {
    path: '/backOffice',
    name: 'backOffice',
    component: backOffice,
    meta: {
      requiresAuth: true, // Définit une méta-information pour exiger l'authentification
    },
  },
  {
    path: '/adminLogin', // Define the route for admin login
    name: 'AdminLogin',
    component: AdminLogin,
  },
];

const router = new VueRouter({
  routes,
  mode: 'history', // This enables clean URLs without hash (#)
});

// Ajoutez un garde de navigation global pour vérifier l'authentification
router.beforeEach(async (to, from, next) => {
  const currentUser = firebase.auth().currentUser; // Récupérez l'utilisateur actuellement connecté

  if (to.matched.some((record) => record.meta.requiresAuth)) {
    // La route nécessite une authentification
    if (!currentUser) {
      // L'utilisateur n'est pas connecté, redirigez-le vers la page de connexion
      next('/adminLogin'); // Remplacez par le chemin de votre page de connexion
    } else {
      // L'utilisateur est connecté, autorisez l'accès à la route
      next();
    }
  } else {
    // La route ne nécessite pas d'authentification, autorisez l'accès directement
    next();
  }
});

export default router;
